<template>
	<div class="index" >
		<custHeader :title="title"></custHeader>
      <div class="logo">
				<img :src="logoimg" alt="logo" class="logoImg" width>
      </div>

			<div class="swiper">
				<mt-swipe :auto="0">
					<mt-swipe-item>
						<ul class="item">
							<li><a href="#/market/marketFinancial"><div class="" style="background-color:rgb(255, 120, 74)">认购</div></a></li>
							<li><a href="#/market/marketFirstJj"><div class="" style="background-color:#4f86f7">优先</div></a></li>
							<li><a href="#/market/marketJj"><div class="" style="background-color:#fb4f14">普通</div></a></li>
							<li><a href="#/jqqd"><div class="" style="background-color:#cb6d51">商城</div></a></li>
							<li><a href="#/charge"><div class="" style="background-color:#3388ff">充值</div></a></li>
							<li><a href="#/takeMoney"><div class="" style="background-color:#cb6d51">提现</div></a></li>
							<!-- <li><div class="" style="background-color:#fc4f08">商城</div></li>
							<li><div class="" style="background-color:#ccc">咨讯</div></li>
							<li><div class="" style="background-color:#fc4f08">公告</div></li> -->
						</ul>
					</mt-swipe-item>
					<!-- <mt-swipe-item>
						2
					</mt-swipe-item> -->
				</mt-swipe>
			</div>
			<div class="" style="height:30vmin">
				<mt-swipe>
					<mt-swipe-item   v-for="(item ,index) in adData">
						<a :href=item.aSrc>
							<img :src=item.imgSrc alt="advertisementPic" width="100%" height="100%">
						</a>
					</mt-swipe-item>
					<!-- <mt-swipe-item id="ad1">
						<img src="../../assets/2276.png" alt="ad1">
					</mt-swipe-item>
					<mt-swipe-item id="ad2">
						<img src="../../assets/2300(1).png" alt="ad1">
					</mt-swipe-item>
					<mt-swipe-item id="ad3">
						<img src="" alt="ad3">
					</mt-swipe-item> -->
				</mt-swipe>
			</div>

			<div class="info" >
				<a :href=item.src v-for="(item,index) in infoData">
					<p>
						{{item.title}}
				</p>
			</a>
			</div>
			<div class="tuijian">
				<mt-cell title="推荐产品" value="更多" :to="{path:'/financialMore',query:{stkBiz:stkBizs.subscribePurchase}}" is-link class="stk-type"></mt-cell>
				<div v-if="listRgsg.length==0" class=" tip-message"> </div>
				<ul class="list-infinite list-infinite-no">
				  <li v-for="(item,index) in listRgsg">
					<router-link :to="{path:'/financial/productDetail',query:{issNo:item.issNo,stkBiz:stkBizs.subscribePurchase}}">
					  <div class="financial-box">
						<div>{{item.stkName}}</div>
						<div class="circle-progress-contain">
						  <circleProgress :num="jindu(item)" ></circleProgress>
						</div>

					  </div>
					  <div class="financial-box little-tile">
						<div>
						  价格:<span clsss="big-info" style="font-size: 18px;color: #f00;">{{item.issPrice | toMoney}}</span>元/<span v-remoteoptionsspan="dicts.unit">{{item.unit}}</span>
						</div>
						<div style="height: 20px;line-height: 22px;">
						  梯度:{{item.baseQty}}

						</div>
						<div style="height: 20px;line-height: 22px;">
						  可购:{{kegou(item)}}

						</div>
					  </div>
					</router-link>
				  </li>
				</ul>
			</div>
  </div>
</template>
<script>
import custHeader from  'components/custHeaderNoBack'
import {
	Swipe,
	SwipeItem
} from 'mint-ui';
import cookieUtil from 'js-cookie'
import circleProgress from 'components/circleProgress.vue'

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
export default{
	name:"index",
	data(){
		return {
			title:"首页",
			logoimg:require('assets/logo2.png'),
			dicts: {
	          unit: {
	            name: "UNIT"
	          }
	        },
			adData:[{"imgSrc":require('assets/banner1.jpg'),"aSrc":"#"},{"imgSrc":require('assets/banner2.jpg'),"aSrc":"#"}],
			infoData:"",
			stkBizs: {
			  subscribe: global.STKBIZ.SUBSCRIBE, //资金申购  默认这个  不做认购申购
			  subscribePurchase: global.STKBIZ.SUBSCRIBE_PURCHASE,
			  declareIssue: global.STKBIZ.DECLARE_ISSUE, //配股申报
			  marketSell: global.STKBIZ.MARKET_SELL, //市值配售
			  privateSell: global.STKBIZ.PRIVATE_SELL //定向配售
		  },
		   listRgsg: [],
		}
	},
	created:function (){
		var self = this
		axiosUtil.getInfo({filePath:"0"}).then(function(data){
			var data = data.data;
			var infoArr = []
			for (var i = 0; i < data.length; i++) {
				infoArr.push({"src":"#/indexInfo/"+data[i].itemNo+"/"+data[i].pageNo,"title":data[i].title})
			}
			self.infoData = infoArr //链接
		})
		var c1 = {
	      order: '', //排序 不填 默认字段
	      stkex: global.scdm, //
	      expectRateFlag: 0, // 预期年绿化率" 0 全部
	      minBuyAmt: 0, //起购金额 0不限
	      periodFlag: 0, //剩余期限//0不限
	      offset: 0,
	      limit: 2,//推荐2个
	      stkBiz: this.stkBizs.subscribePurchase
	    };
	    axiosUtil.upCltIssStkQry(c1).then(function(data) {
	      self.listRgsg = data.data.rows;
	    })
		//axiosUtil.getAD().then(function(data){
	 // 	self.ad = data //广告
	 // })
 },
 components:{
	 circleProgress,
	 custHeader
 },
 methods:{
	 kegou: function(item) {
	   if (!item.applyAmt)
		 item.applyAmt = 0;
	   return item.issSize.subtr(item.applyAmt)
	 },
	 jindu: function(item) {
	   // return item.applyAmt.div(item.issSize)
	   if (!item.applyAmt)
		 item.applyAmt = 0;
	   return item.applyAmt.div(item.issSize)
	 },
 }


}
</script>
<style scoped>
body{

}
.index{
	height:100%;
	background-color: #eee;
}
.logo{
	height: 12%;
	background-color:#fff;
	margin-bottom: 2%;
}
.logoImg{

	width: 200px;
	padding: 2% 2% 2% 3%;
	margin-left: 5%;
}

.swiper{
	height: 40vmin;
	background-color:#fff;
	padding-top: 2%;
}
.mint-swipe{
	height: 100%;
}
.item{
	height: 100%;
}
.item li {
	list-style: none;
	/*float: left;*/
	display: inline-block;
	width: 23%;
	padding-bottom: 2%;
}
.item li a{
	text-decoration: none;
}

.item li div{
	/*width: 79%;
	height: 87%;*/
	width: 15vmin;
	height: 15vmin;
	margin: 0 auto;
	border-radius: 100%;
	text-align: center;
	line-height:15vmin;
	color:#fff;
}

#ad1 img,#ad2 img,#ad3 img{
	width: 100%;
	height: 100%
}
.info{
	margin-top: 8%;
}


.info p{
	background-color:#fff;
	line-height: 34px;
	text-indent: 2em;
	margin-top: 2%;
}

.stk-type {
  background: #eee;
}
.circle-progress-contain>div {
  float: right;
}
.list-infinite-no{
padding-bottom:	48px;
}
.tip-message{
 text-align:center;
 color:#999;	
}
</style>



